import React from 'react';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import Tooltip from '@material-ui/core/Tooltip';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import AnDJ from '../src/andj.jpg';

const useStyles = makeStyles({
    root: {
        width: '100%',
        maxWidth: 700,
        margin: '0 auto',
    },
});

export default function Main() {
    const classes = useStyles();
    const HtmlTooltip = withStyles(theme => ({
        tooltip: {
            backgroundColor: '#f5f5f9',
            color: 'rgba(0, 0, 0, 0.87)',
            maxWidth: 300,
            fontSize: theme.typography.pxToRem(12),
            border: '1px solid #dadde9',
            placement: 'right'
        },
    }))(Tooltip);
    return (
        <div className={classes.root}>
            <div style={{margin:'20px'}}>
                <Typography variant="h2" gutterBottom>
                    <b>Hi,</b>
                </Typography>
                <Typography variant="h4" gutterBottom>
                    <b>I'm Junduo Dong (董峻铎).</b>
                </Typography>
                <Typography variant="caption" display="block" gutterBottom style={{marginTop:'30px', fontSize:'20px'}}>
                    I am an undergraduate from cug, and an independent developer. Many funny repositories were open source and available on
                    <Button color="primary" className={classes.button} href={'https://github.com/An-DJ'}>
                        GitHub
                    </Button>.
                </Typography>
                <Typography variant="caption" display="block" gutterBottom style={{marginTop:'30px', fontSize:'20px'}}>
                    I love basic computer science research, such as OS, compiler, database and so on.
                    Now my research is about distributed system and distributed computing.
                </Typography>
                <Typography variant="caption" display="block" gutterBottom style={{marginTop:'30px', fontSize:'20px'}}>
                    Also i have some work experience in infrastructure design and micro services.
                    An open source tracing system will be released soon.
                </Typography>
                <Typography variant="caption" display="block" gutterBottom style={{marginTop:'30px', fontSize:'20px'}}>
                    You can visit my
                    <Button color="primary" className={classes.button} href={'https://blog.andj.me/'}>
                        blog
                    </Button> and
                    <Button color="primary" className={classes.button} href={'https://weibo.com/u/6038332023'}>
                        weibo
                    </Button>. If you want to contact me, you can send e-mail to
                    <Chip
                        label="andj4cn@gmail.com"
                        variant="outlined"
                    /> or
                    <Chip
                        label="an_dj@outlook.com"
                        variant="outlined"
                    />.
                </Typography>
                <HtmlTooltip
                    title={
                        <React.Fragment>
                            Copyright (c) 2019 AnDJ. All rights reserved.<br/>
                                Author: AnDJ (Junduo Dong)<br/>
                                Email: andj4cn@gmail.com<br/>
                                Use of this source code is governed by MIT license.
                        </React.Fragment>
                    }
                    style={{marginTop:'30px', float:'right'}}
                >
                    <Chip
                        size="small"
                        avatar={<Avatar alt="andj" src={AnDJ} />}
                        label="AnDJ"
                        clickable
                        color="primary"
                    />
                </HtmlTooltip>
                <br/><br/><br/><br/>
            </div>
        </div>
    );
}
